<!-- 搜索 -->
<template>
<div class='searchView'>
  <div class="searchbox">
    <van-search
     v-model="searchValue"
     right-icon="" 
     left-icon=""
     shape="round"
     show-action
     placeholder="请输入搜索关键词"
     @cancel="onCancel"
     @input="inputing"
     @keydown.enter="displayResult"
      />
   </div>
   <div class="hotSearch">
   <div class="hotSearch-title">热门搜索</div>
   <div class="hotSearch-words">
      <div class="words" v-for="(word,index) in hotWords" :key="index" @click.stop="suggestWord(word.name)">
        {{index+1}} {{word.name}}
      </div>
   </div>
   </div>
   <div class="hirstory">
   <div class="hirstory-title">最近搜索</div>
   <div class="hirstory-words"></div>
   </div>
</div>
</template>

<script>
import { Toast } from 'vant';
// import SearchSuggest from '@/components/SearchSuggest.vue'

export default {

props:{},
data() {
//这里存放数据
return {
  searchValue:'',
  hotWords:[],
  num:1,
};
},
//监听属性 类似于data概念
computed: {},
//监控data中的数据变化
watch: {},
//方法集合
methods: {
 onCancel() {
      Toast('取消');
      this.$router.go(-1);
    },
    async getHotWords(){
        await this.axios.get('/search/getHotWords').then(re=>{
           this.hotWords=re.list;
           console.log('this.hotWords',this.hotWords);
        })
    },
    displayResult(){
         this.$router.push({
       name:'SearchResult',
       params:{
         word:this.searchValue,
       }
     })
    },
    suggestWord(sw){
     this.searchValue=sw;
     this.displayResult();
    },
    inputing(){
       if(this.searchValue==' '||this.searchValue.length<=0){
          Toast.fail('请勿输入空内容');
    }else{
     this.componentName="SearchSuggest";
     }
    }    
},
components: {
  // SearchSuggest
},
//生命周期 - 创建完成（可以访问当前this实例）
created() {
  this.getHotWords();
},
//生命周期 - 挂载完成（可以访问DOM元素）
mounted() {

},
beforeCreate() {}, //生命周期 - 创建之前
beforeMount() {}, //生命周期 - 挂载之前
beforeUpdate() {}, //生命周期 - 更新之前
updated() {}, //生命周期 - 更新之后
beforeDestroy() {}, //生命周期 - 销毁之前
destroyed() {}, //生命周期 - 销毁完成
activated() {}, //如果页面有keep-alive缓存功能，这个函数会触发


}
</script>
<style lang='scss' scoped>
.searchView{
    z-index: 100;
    
    font-size: 14px;
    }
.hotSearch {
    margin-top: 25px;
    padding: 0 10px 0 20px;
  .hotSearch-title {
    padding-bottom: 20px;
    color: grey;
  }
  .hotSearch-words {
      display: flex;
      flex-wrap: wrap;
      .words{
        padding: 10px 50px 20px 25px;
      }
    
  }
}
.hirstory {
    padding: 0 10px 0 20px;
    margin-top: 20px;
  .hirstory-title {
    color: grey;

  }

  .hirstory-words {

  }
}
</style>